@import "ScssHelpers/helpers_all";

.review {
	margin-left: auto;
	margin-top: 0.3rem;

	font-size: 1rem;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.review__head {
	@include flex_block(row, nowrap, flex-start, center);

	&:not(:last-child) {
		margin-bottom: 0.5rem;
	}
}

.review__logo_title {
	font-size: 1.2rem;
	font-weight: 500;
	letter-spacing: 0.02rem;
	text-transform: uppercase;
	color: var(--gray_color);

	&:not(:last-child) {
		margin-right: 2.5rem;
	}
}

.review__stars {
	width: 9.7rem;
	height: 1.4rem;
}

.review__stars_img {
	@extend %contain_img;
}

.review__row {
	@include flex_block(row, nowrap, flex-start, flex-end);
}

.review_logo {
	width: 8.5rem;
	height: 2.4rem;

	&:not(:last-child) {
		margin-right: 3.3rem;
	}
}

.review_logo__in {
	@extend %contain_img;
}

.review__amount {
	color: var(--gray_color);
	font-size: 1.6rem;
	line-height: 1;
	font-weight: 500;
	letter-spacing: 0.03rem;
	text-transform: uppercase;
}
