@import "ScssHelpers/helpers_all";

.case__row {
	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, stretch, normal);
	}
}

.case__col {
	@include bp($point_4, min) {
		flex: 0 1 50%;
		max-width: 50%;
	}
}

.case_description {
	@include bp($point_2, min) {
		padding: 11.5rem var(--gl-indent) 18rem 17.5rem;

		font-size: 2rem;
		line-height: 1.45;
	}

	@include bp($point_3) {
		padding: 8rem var(--gl-indent) 10rem;

		font-size: 1.4rem;
		line-height: 1.35;
	}

	@include bp($point_4 - 1) {
		padding: 5.4rem var(--gl-indent) 6.5rem;
	}

	color: var(--primary_color);
	font-weight: 300;

	h2 {
		color: var(--primary_color);
		font-weight: 600;
	
		@include bp($point_2, min) {
			font-size: 4.8rem;
	
			&:not(:last-child) {
				margin-bottom: 3.9rem;
			}
		}
	
		@include bp($point_3, $point_4) {
			font-size: 4rem;
	
			&:not(:last-child) {
				margin-bottom: 3.4rem;
			}
		}
	
		@include bp($point_4 - 1) {
			font-size: 1.8rem;
	
			&:not(:last-child) {
				margin-bottom: 1.5rem;
			}
		}
	}

	a {
		@extend %transition_color;

		color: var(--blue_color_2);

		&:hover,
		&:focus {
			text-decoration: none;
			color: var(--orange_color);
		}
	}

	ol {
		counter-reset: fact;

		@include bp($point_4, min) {
			max-width: 60.2rem;
	
			&:not(:last-child) {
				margin-bottom: 14.2rem;
			}
		}
	
		@include bp($point_4 - 1) {
			&:not(:last-child) {
				margin-bottom: 3.8rem;
			}
		}

		li {
			position: relative;

			@include flex_block(row, nowrap, flex-start, center, center);

			border-top: 2px solid var(--light_blue_2);
			
			&:last-child {
				border-bottom: 2px solid var(--light_blue_2);
			}
			
			&::before {
				counter-increment: fact;
				content: counters(fact, '', decimal-leading-zero);
		
				position: absolute;
				top: 50%;
				left: 0;
		
				transform: translateY(-50%);
				background-color: var(--light_blue);
		
				font-family: $font_3;
				color: var(--primary_color);
		
				@include flex_block(row, nowrap, center, center, center);
			}
		
			@include bp($point_2, min) {
				padding: 2.4rem 0 2.1rem 11.6rem;
				min-height: 11rem;
		
				&::before {
					width: 7rem;
					height: 7rem;
		
					font-size: 2.5rem;
				}
			}
		
			@include bp($point_3) {
				padding: 1.4rem 0 1.2rem 7rem;
				min-height: 7rem;
		
				&::before {
					width: 4.8rem;
					height: 4.8rem;
		
					font-size: 1.6rem;
				}
			}
		}
	}

	strong{
		font-weight: 700;
	}
}

