@import "ScssHelpers/helpers_all";

.tools {
	background-color: var(--light_blue);

	height: 100%;

	@include bp($point_2, min) {
		padding: 11.5rem 13rem 18rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 8rem var(--gl-indent) 10rem;
	}

	@include bp($point_4 - 1) {
		padding: 5.4rem var(--gl-indent) 6.5rem;
	}
}

.tools__title {
	color: var(--primary_color);
	font-weight: 600;

	@include bp($point_2, min) {
		font-size: 4.8rem;

		&:not(:last-child) {
			margin-bottom: 3.9rem;
		}
	}

	@include bp($point_3, $point_4) {
		font-size: 4rem;

		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;

		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}
}

.tools__slider {
	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}

		@include flex_block(row, wrap, flex-start);

		.swiper-wrapper {
			@include flex_block(row, wrap, flex-start);
		}
	}

	@include bp($point_4 - 1) {
		&.swiper {
			overflow: visible;
		}

		&:not(:last-child) {
			margin-bottom: 3.8rem;
		}
	}
}

.tools__slide {
	position: relative;

	overflow: hidden;

	@include bp($point_2, min) {
		flex: 0 1 33.333%;
	}

	@include bp($point_3, $point_4) {
		flex: 0 1 50%;
	}
}

.tools__slide:hover,
.tools__slide:focus {
	.tools_image__name {
		opacity: 1;
	}
}

.tools_image {
	padding-bottom: 100%;
}

.tools_image__name {
	@extend %transition_opacity;

	position: absolute;
	top: -0.1rem;
	left: -0.1rem;
	z-index: 1;

	width: calc(100% + 0.2rem);
	height: calc(100% + 0.2rem);
	padding: 2rem;
	overflow-y: auto;
	text-align: center;

	background-color: rgba(14, 30, 69, 0.8);
	color: var(--white_color);
	opacity: 0;

	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		line-height: 1.35;
	}
}

.tools_image__in {
	@extend %contain_img;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.tools__descr {
	max-width: 54rem;

	color: var(--gray_color);

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.5;
	}
	@include bp($point_3) {
		font-size: 1.4rem;
		line-height: 1.35;
	}
}

// Progress bar
.tools_progress {
	@include bp($point_4, min) {
		display: none;
	}

	@include bp($point_4 - 1) {
		@include flex_block(row, nowrap, flex-start, center, center);

		&:not(:last-child) {
			margin-bottom: 3.1rem;
		}
	}
}

.tools_progress__value {
	color: var(--blue_color_2);
	font-size: 1.3rem;
}

.tools_progress__bar {
	@extend %global_flex_block_row_nowrap_flex-start;

	margin: 0 1.2rem;

	flex-grow: 1;
	height: 0.2rem;

	background-color: var(--border_grey_color);

	.swiper-pagination-progressbar-fill {
		flex-grow: 1;
		height: inherit;

		transform-origin: left top;
		background-color: var(--blue_color);
	}
}
